/* === GENERAL STYLES === */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background: #000;
  color: #fff;
}

.App {
  padding-bottom: 60px;
  background-color: #000;
}

.app-title {
  text-align: center;
  padding: 1rem;
  background: #111;
  color: #ffd700;
  font-size: 1.8rem;
}

/* === DRINK GRID & CARD === */
.drink-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 1.2rem;
  padding: 1.5rem 1rem;
}

.drink-card {
  background-color: #111;
  border: 1px solid #444;
  border-radius: 16px;
  padding: 0.5rem;
  text-align: center;
  color: #ffd700;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 320px;
  transition: transform 0.2s, box-shadow 0.2s;
}

.drink-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 0 12px #ffd70088;
}

.drink-img {
  width: 100%;
  height: 240px;
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 0.5rem;
}

.drink-card h3 {
  font-size: 0.85rem;
  margin: 0.3rem 0 0.2rem;
  color: #ffd700;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.drink-card p {
  font-size: 0.7rem;
  color: #ffd700;
  margin: 0.2rem 0;
  line-height: 1.3;
  overflow: visible;
  height: auto;
}

.drink-card button {
  margin-top: auto;
  padding: 6px 12px;
  font-size: 0.75rem;
  background-color: #ffd700;
  color: #000;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.2s, transform 0.2s;
}

.drink-card button:hover {
  background-color: #e5c100;
  transform: scale(1.05);
}

/* === CATEGORY BUTTONS === */
.category-buttons-container {
  display: flex;
  overflow-x: auto;
  padding: 0.5rem;
  background: #111;
  scrollbar-width: none;
}

.category-buttons-container::-webkit-scrollbar {
  display: none;
}

.category-button {
  margin-right: 0.5rem;
  padding: 3px 8px;
  font-size: 0.6rem;
  background: #222;
  border: 1px solid #ffd700;
  color: #ffd700;
  border-radius: 12px;
  cursor: pointer;
  white-space: nowrap;
  transition: background 0.2s, transform 0.2s;
}

.category-button:hover {
  background-color: #333;
  transform: scale(1.05);
}

.category-button.active {
  background-color: #ffd700;
  color: #000;
}

/* === CART === */
.cart-list {
  list-style: none;
  padding: 1rem;
  margin: 0 auto;
  max-width: 600px;
  background-color: #111;
  border-radius: 12px;
  box-shadow: 0 2px 10px #ffd70044;
}

.cart-item {
  display: flex;
  align-items: center;
  border-bottom: 1px solid #333;
  padding: 0.5rem 0;
  transition: background-color 0.3s;
}

.cart-item:hover {
  background-color: #222;
}

.cart-img {
  width: 100px;
  height: 160px;
  border-radius: 8px;
  object-fit: cover;
  margin-right: 0.5rem;
}

.cart-details {
  flex: 1;
  color: #ffd700;
  font-size: 0.8rem;
}

.cart-buttons button {
  margin-right: 5px;
  padding: 4px 8px;
  font-size: 0.7rem;
  background-color: #444;
  color: #ffd700;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background 0.2s, transform 0.2s;
}

.cart-buttons button:hover {
  background-color: #666;
  transform: scale(1.05);
}

/* === CHECKOUT === */
.checkout-form {
  margin-top: 1rem;
  padding: 1rem;
  background: #111;
  border-radius: 8px;
}

.checkout-form h3 {
  color: #ffd700;
  margin-bottom: 0.5rem;
}

.checkout-form select,
.checkout-form button {
  padding: 6px 10px;
  font-size: 0.8rem;
  border-radius: 6px;
  border: none;
  margin-top: 0.5rem;
  background-color: #333;
  color: #ffd700;
  transition: background 0.2s, transform 0.2s;
}

.checkout-form button {
  background-color: #ffd700;
  color: #000;
  cursor: pointer;
}

.checkout-form button:hover {
  background-color: #e5c100;
  transform: scale(1.05);
}

/* === INPUT FIELDS & SEARCH === */
input[type="text"],
input[type="password"],
select {
  background-color: #111;
  color: #ffd700;
  border: 1px solid #444;
  border-radius: 6px;
  padding: 6px;
}

input[type="text"]::placeholder,
input[type="password"]::placeholder {
  color: #999;
}

/* === ADMIN PANEL BUTTONS === */
.admin-panel {
  padding: 1rem 1.5rem;
  background-color: #111;
  border-radius: 12px;
  max-width: 900px;
  margin: 0 auto;
  box-shadow: 0 2px 12px #ffd70044;
}

.admin-panel button {
  margin: 0.3rem;
  padding: 8px 14px;
  font-size: 0.85rem;
  background-color: #222;
  color: #ffd700;
  border: 1px solid #ffd700;
  border-radius: 10px;
  cursor: pointer;
  transition: background 0.3s, color 0.3s, transform 0.2s;
}

.admin-panel button:hover {
  background-color: #ffd700;
  color: #000;
  transform: scale(1.1);
}

/* === ADMIN ORDERS TABLE === */
.admin-orders-table {
  width: 100%;
  border-collapse: collapse;
  font-size: 0.85rem;
  margin-top: 1rem;
  background-color: #1a1a1a;
  border-radius: 10px;
  box-shadow: 0 0 10px #ffd70077;
}

.admin-orders-table th,
.admin-orders-table td {
  border: 1px solid #555;
  padding: 8px 12px;
  text-align: center;
  color: #ffd700;
}

.admin-orders-table th {
  background-color: #222;
  color: #ffd700;
}

.admin-orders-table tr:hover {
  background-color: #333;
  color: #fff;
}

/* === TABS CONTAINER - HORIZONTAL === */
.tabs-container {
  display: flex;
  justify-content: center;
  background-color: #111;
  border-bottom: 2px solid #ffd700;
  margin: 0 1rem;
  border-radius: 8px 8px 0 0;
  overflow-x: auto;
  scrollbar-width: none;
}

.tabs-container::-webkit-scrollbar {
  display: none;
}

.tab-button {
  flex: 1 0 auto;
  padding: 10px 20px;
  background-color: #222;
  color: #ffd700;
  border: none;
  cursor: pointer;
  font-weight: bold;
  font-size: 1rem;
  border-right: 1px solid #444;
  transition: background-color 0.3s, color 0.3s;
  user-select: none;
}

.tab-button:last-child {
  border-right: none;
}

.tab-button:hover {
  background-color: #333;
  color: #fff;
}

.tab-button.active {
  background-color: #ffd700;
  color: #000;
  box-shadow: 0 4px 8px #ffd70088;
  border-bottom: 4px solid #000;
}
